// Images arenas
import arenaBgBalcony from '../../../images/arenas/balcony.png';
import arenaBgBank from '../../../images/arenas/bank.png';
import arenaBgBelltower from '../../../images/arenas/belltower.png';
import arenaBgBridge from '../../../images/arenas/bridge.png';
import arenaBgGraveyard from '../../../images/arenas/graveyard.png';
import arenaBgJadesDesert from '../../../images/arenas/jades-desert.png';
import arenaBgNoobsDorfen from '../../../images/arenas/noobs-dorfen.png';
import arenaBgPit3 from '../../../images/arenas/pit3.png';
import arenaBgRooftop from '../../../images/arenas/rooftop.png';
import arenaBgScislacBusorez from '../../../images/arenas/scislac-busorez.png';
import arenaBgScorpionsLairArenas from '../../../images/arenas/scorpions-lair-arenas.png';
import arenaBgSmokesPortal from '../../../images/arenas/smokes-portal.png';
import arenaBgSoulChamber from '../../../images/arenas/soul-chamber.png';
import arenaBgStreet from '../../../images/arenas/street.png';
import arenaBgSubway from '../../../images/arenas/subway.png';
import arenaBgTemple from '../../../images/arenas/temple.png';
import arenaBgTheCave from '../../../images/arenas/the-cave.png';
import arenaBgWaterfront from '../../../images/arenas/waterfront.png';
// Sounds
import mk3SoundArenaBg from '../../../sounds/arena/mk3-sound-arena-bg.mp3';
import mk3SoundArenaBg2 from '../../../sounds/arena/mk3-sound-arena-bg2.mp3';
import mk3SoundArenaBg3 from '../../../sounds/arena/mk3-sound-arena-bg3.mp3';
import soundFight from '../../../sounds/game/mk3-sound-fight.mp3';
import soundActionMenuHover from '../../../sounds/game/mk3-sound-action-menu-hover.mp3';
import soundActionMenuSelect from '../../../sounds/game/mk3-sound-action-menu-select.mp3';
import soundMortalKombat from '../../../sounds/game/mk3-sound-mortal-kombat.mp3';
import soundHit from '../../../sounds/player-actions/mk3-sound-hit.mp3';
import soundHit2 from '../../../sounds/player-actions/mk3-sound-hit2.mp3';
import soundDefenceM from '../../../sounds/player-actions/mk3-sound-defence.mp3';
import soundDefenceG from '../../../sounds/player-actions/mk3-sound-defence-g.mp3';
import soundDamageM from '../../../sounds/player-actions/mk3-sound-damage.mp3';
import soundDamageG from '../../../sounds/player-actions/mk3-sound-damage-g.mp3';
import soundDamage2M from '../../../sounds/player-actions/mk3-sound-damage2.mp3';
import soundDamage2G from '../../../sounds/player-actions/mk3-sound-damage2-g.mp3';
import soundDamage3M from '../../../sounds/player-actions/mk3-sound-damage3.mp3';
import soundDamage3G from '../../../sounds/player-actions/mk3-sound-damage3-g.mp3';
import soundRunning from '../../../sounds/player-actions/mk3-sound-running.mp3';
import soundAttackM from '../../../sounds/player-actions/mk3-sound-attack.mp3';
import soundAttackG from '../../../sounds/player-actions/mk3-sound-attack-g.mp3';
// Sounds wins
import soundWinsShangtsung from '../../../sounds/wins/mk3-sound-wins-shangtsung.mp3';
import soundWinsSindel from '../../../sounds/wins/mk3-sound-wins-sindel.mp3';
import soundWinsJax from '../../../sounds/wins/mk3-sound-wins-jax.mp3';
import soundWinsKano from '../../../sounds/wins/mk3-sound-wins-kano.mp3';
import soundWinsLiukang from '../../../sounds/wins/mk3-sound-wins-liukang.mp3';
import soundWinsSonya from '../../../sounds/wins/mk3-sound-wins-sonya.mp3';
import soundWinsStryker from '../../../sounds/wins/mk3-sound-wins-stryker.mp3';
import soundWinsSmoke from '../../../sounds/wins/mk3-sound-wins-smoke.mp3';
import soundWinsSubzero from '../../../sounds/wins/mk3-sound-wins-subzero.mp3';
import soundWinsCyrax from '../../../sounds/wins/mk3-sound-wins-cyrax.mp3';
import soundWinsSektor from '../../../sounds/wins/mk3-sound-wins-sektor.mp3';
import soundWinsNightwolf from '../../../sounds/wins/mk3-sound-wins-nightwolf.mp3';
import soundWinsSheeva from '../../../sounds/wins/mk3-sound-wins-sheeva.mp3';
import soundWinsKunglao from '../../../sounds/wins/mk3-sound-wins-kunglao.mp3';
import soundWinsKabal from '../../../sounds/wins/mk3-sound-wins-kabal.mp3';
import soundWinsMotaro from '../../../sounds/wins/mk3-sound-wins-motaro.mp3';
import soundWinsShaokahn from '../../../sounds/wins/mk3-sound-wins-shaokahn.mp3';
/** –ü—ñ–¥–∫–ª—é—á–µ–Ω–Ω—è –º–æ–¥—É–ª—ñ–≤. */
import Logs from './Logs.js';
import { ATTACK, HEROES, HIT, ARENAS, ARENASSOUNDS, HITTYPE, DEFENCETYPE, ANIMATE } from '../constants';
import { getRandom, createNewTag, showPage, delay, camelize, createSoundTrack } from '../utils';
import { clearGarbageTags, generateHeroesCollection, getPlayer, formattingPlayerName } from '../logic';

const arenasBg = {
    arenaBgBalcony,
    arenaBgBank,
    arenaBgBelltower,
    arenaBgBridge,
    arenaBgGraveyard,
    arenaBgJadesDesert,
    arenaBgNoobsDorfen,
    arenaBgPit3,
    arenaBgRooftop,
    arenaBgScislacBusorez,
    arenaBgScorpionsLairArenas,
    arenaBgSmokesPortal,
    arenaBgSoulChamber,
    arenaBgStreet,
    arenaBgSubway,
    arenaBgTemple,
    arenaBgTheCave,
    arenaBgWaterfront,
};

const soundsArenasBg = {
    mk3SoundArenaBg,
    mk3SoundArenaBg2,
    mk3SoundArenaBg3,
};

const soundsWins = {
    soundWinsShangtsung,
    soundWinsSindel,
    soundWinsJax,
    soundWinsKano,
    soundWinsLiukang,
    soundWinsSonya,
    soundWinsStryker,
    soundWinsSmoke,
    soundWinsSubzero,
    soundWinsCyrax,
    soundWinsSektor,
    soundWinsNightwolf,
    soundWinsSheeva,
    soundWinsKunglao,
    soundWinsKabal,
    soundWinsMotaro,
    soundWinsShaokahn,
};

class Game {
    constructor({player1Id, player2Id, $arena, $chat}) {
        this.playersCollection1 = generateHeroesCollection(HEROES);
        this.playersCollection2 = generateHeroesCollection(HEROES);
        this.player1Id = player1Id;
        this.player2Id = player2Id;
        this.logs = new Logs($chat);
        this.$arena = $arena;
        this.$form = document.querySelector('form.control');
        this.$btn = this.$form.querySelector('.button');
    }
    
    /**
     * –§—É–Ω–∫—Ü—ñ—è –¥–ª—è —Å—Ç–∞—Ä—Ç—É –≥—Ä–∏.
     */
    async start() {
        const player1 = getPlayer(this.playersCollection1, this.player1Id); // –ì—Ä–∞–≤–µ—Ü—å ‚Ññ1. 
        const player2 = getPlayer(this.playersCollection2, this.player2Id); // –ì—Ä–∞–≤–µ—Ü—å ‚Ññ2. 

        player1.playerNumber = 1; // –ü—Ä–∏–∑–Ω–∞—á–µ–Ω–Ω—è –≥—Ä–∞–≤—Ü—é –Ω–æ–º–µ—Ä–∞ ‚Ññ1.
        player2.playerNumber = 2; // –ü—Ä–∏–∑–Ω–∞—á–µ–Ω–Ω—è –≥—Ä–∞–≤—Ü—é –Ω–æ–º–µ—Ä–∞ ‚Ññ2.
        
        if (player1.name === player2.name) {
            player1.name = player1.name + '-' + player1.playerNumber;
            player2.name = player2.name + '-' + player2.playerNumber;
        }

        this.arenaAutoBgSelection(); // –ü—ñ–¥–±—ñ—Ä —Ñ–æ–Ω—É –∞—Ä–µ–Ω–∏ (–≤–∏–∫–æ–Ω—É—î—Ç—å—Å—è –≤–∏–ø–∞–¥–∫–æ–≤–æ).

        this.$player1 = this.createPlayer(player1); // –°—Ç–≤–æ—Ä–µ–Ω–Ω—è –≥—Ä–∞–≤—Ü—è.
        this.$player2 = this.createPlayer(player2); // –°—Ç–≤–æ—Ä–µ–Ω–Ω—è –≥—Ä–∞–≤—Ü—è.
        
        this.addingToArena(this.$player1); // –î–æ–¥–∞–≤–∞–Ω–Ω—è –≥—Ä–∞–≤—Ü—è –Ω–∞ –∞—Ä–µ–Ω—É.
        this.addingToArena(this.$player2); // –î–æ–¥–∞–≤–∞–Ω–Ω—è –≥—Ä–∞–≤—Ü—è –Ω–∞ –∞—Ä–µ–Ω—É.
        
        this.submitResult(player1, player2);

        this.logs.generateLogs({type: 'start', attackingPlayer: player1, defensivePlayer: player2}); // –í –ª–æ–≥—ñ –≤–∏–≤–æ–¥–∏—Ç—å—Å—è –ø–µ—Ä—à–∞ —Ñ—Ä–∞–∑–∞.

        await delay(0).then(() => showPage()); // –í—ñ–¥–±—É–≤–∞—î—Ç—å—Å—è –ø–ª–∞–≤–Ω–µ –≤—ñ–¥–æ–±—Ä–∞–∂–µ–Ω–Ω—è —Å—Ç–æ—Ä—ñ–Ω–∫–∏.            
        await delay(2500).then(() => this.createGong()); // –í—ñ–¥–æ–±—Ä–∞–∑–∏—Ç–∏ –≥–æ–Ω–≥.
        await delay(2000).then(() => this.removeGong()); // –ü—Ä–∏–±—Ä–∞—Ç–∏ –≥–æ–Ω–≥.
        await delay(500).then(() => this.showControls()); // –í—ñ–¥–æ–±—Ä–∞–∑–∏—Ç–∏ –ø–∞–Ω–µ–ª—å –∫–µ—Ä—É–≤–∞–Ω–Ω—è.        

        this.createSoundFormHover();
        this.formControlCheck();
    }

    /**
     * –§—É–Ω–∫—Ü—ñ—è –¥–ª—è –ø—Ä–∏—Ö–æ–≤—É–≤–∞–Ω–Ω—è –ø–æ–≤—ñ–¥–æ–º–ª–µ–Ω–Ω—è –ø—Ä–æ –ø–æ—á–∞—Ç–æ–∫ –±–∏—Ç–≤–∏.
     */
    removeGong = () => {
        document.querySelector('.gong-popup').remove();
    }

    /**
     * –§—É–Ω–∫—Ü—ñ—è –¥–ª—è –≤—ñ–¥–æ–±—Ä–∞–∂–µ–Ω–Ω—è —Ñ–æ—Ä–º–∏ –∫–µ—Ä—É–≤–∞–Ω–Ω—è.
     */
    showControls = () => {
        this.$form.classList.remove('disabled');
    }
    
    /**
     * –§—É–Ω–∫—Ü—ñ—è –¥–ª—è –ø—Ä–∏—Ö–æ–≤—É–≤–∞–Ω–Ω—è —Ñ–æ—Ä–º–∏ –∫–µ—Ä—É–≤–∞–Ω–Ω—è.
     */
    hideControls = () => {
        this.$form.classList.add('disabled');
    }

    /**
     * –§—É–Ω–∫—Ü—ñ—è –¥–ª—è –≤—ñ–¥–æ–±—Ä–∞–∂–µ–Ω–Ω—è –ø–æ–≤—ñ–¥–æ–º–ª–µ–Ω–Ω—è –ø—Ä–æ –ø–æ—á–∞—Ç–æ–∫ –±–∏—Ç–≤–∏.
     */
    createGong = () => {
        const $fightPopup = createNewTag({
            tagName: 'div',
            className: 'gong-popup',
        });

        createSoundTrack({
            className: 'sound-fight',
            src: soundFight,
        });

        this.arenaAutoSoundSelection();       

        document.querySelector('body').append($fightPopup);
    }

    /**
     * –§—É–Ω–∫—Ü—ñ—è –¥–ª—è –∞–≤—Ç–æ–º–∞—Ç–∏—á–Ω–æ–≥–æ –≤–∏–±–æ—Ä—É —Ñ–æ–Ω—É –∞—Ä–µ–Ω–∏.
     */
    arenaAutoBgSelection = () => {
        const arenaRandomImg = getRandom(ARENAS.length) - 1;
        const arenaNameImg = ARENAS[arenaRandomImg];
        const arenaSrc = arenasBg[`arenaBg${camelize(arenaNameImg, true)}`];

        this.$arena.style.backgroundImage = `url(${arenaSrc})`;
    }
 
    /**
     * –§—É–Ω–∫—Ü—ñ—è –¥–ª—è –∞–≤—Ç–æ–º–∞—Ç–∏—á–Ω–æ–≥–æ –≤–∏–±–æ—Ä—É —Ñ–æ–Ω–æ–≤–æ–≥–æ –∑–≤—É–∫—É –Ω–∞ –∞—Ä–µ–Ω—ñ.
     */
    arenaAutoSoundSelection = () => {
        const arenaRandomSounds = getRandom(ARENASSOUNDS.length) - 1;
        const arenaNameSound = ARENASSOUNDS[arenaRandomSounds];        
        
        createSoundTrack({
            className: 'sound-arena-bg',
            src: soundsArenasBg[`${camelize(arenaNameSound)}`],
            loop: true,
            volume: 0.5,
        });
    }

    /**
     * –§—É–Ω–∫—Ü—ñ—è –¥–ª—è —Å—Ç–≤–æ—Ä–µ–Ω–Ω—è –∑–≤—É–∫–æ–≤–æ–≥–æ —Å—É–ø—Ä–æ–≤–æ–¥–∂–µ–Ω–Ω—è –ø—Ä–∏ –Ω–∞–≤–µ–¥–µ–Ω–Ω—ñ –Ω–∞ –∫–Ω–æ–ø–∫–∏ —Ñ–æ—Ä–º–∏.
     */
     createSoundFormHover = () => {
        this.$form.addEventListener('mouseover', (e) => {
            const elem = e.target.closest('.target-list label span');

            if (elem) {
                createSoundTrack({
                    className: 'sound-action-hover',
                    src: soundActionMenuHover,
                });
            }
        });
    }

    /**
     * –§—É–Ω–∫—Ü—ñ—è –¥–ª—è —Å—Ç–≤–æ—Ä–µ–Ω–Ω—è –∑–≤—É–∫–æ–≤–æ–≥–æ —Å—É–ø—Ä–æ–≤–æ–¥–∂–µ–Ω–Ω—è –ø—Ä–∏ –Ω–∞—Ç–∏—Å–∫–∞–Ω–Ω—ñ –Ω–∞ –∫–Ω–æ–ø–∫–∏ —Ñ–æ—Ä–º–∏.
     */
    createSoundFormClick = () => {                
        createSoundTrack({
            className: 'sound-action-select',
            src: soundActionMenuSelect,
        });

        clearGarbageTags('.sound-action-hover');                                   
    }
    
    /**
     * –§—É–Ω–∫—Ü—ñ—è –¥–ª—è –∞–∫—Ç–∏–≤—É–≤–∞–Ω–Ω—è –∫–Ω–æ–ø–∫–∏ 'Fight' –≤ —Ñ–æ—Ä–º—ñ.
     */
    formControlCheck = () => {
        const approvList = new Set();

        this.$form.addEventListener('click', (e) => {            
            const $span = e.target.closest('.target-list label span');
            const $button = e.target.closest('form .button');

            if ($span) {
                if ($span.dataset.type === 'hit') {
                    approvList.add($span.dataset.type);
                }
    
                if ($span.dataset.type === 'defence') {
                    approvList.add($span.dataset.type);
                }
                
                if (approvList.size >= 2) {
                    this.$btn.disabled = false;
                } 
                
                this.createSoundFormClick();                
            } 

            if ($button) {
                approvList.clear();
            }
        });
    }

    /**
     * –§—É–Ω–∫—Ü—ñ—è –¥–ª—è —Å—Ç–≤–æ—Ä–µ–Ω–Ω—è –Ω–æ–≤–æ–≥–æ –≥—Ä–∞–≤—Ü—è.
     * 
     * @param {object} player - –û–±'—î–∫—Ç –∑ –¥–∞–Ω–∏–º–∏ –≥—Ä–∞–≤—Ü—è. 
     * @param {string} player.name - –Ü–º'—è –≥—Ä–∞–≤—Ü—è. 
     * @param {number} player.playerNumber - –ù–æ–º–µ—Ä –≥—Ä–∞–≤—Ü—è, –º–æ–∂–µ –±—É—Ç–∏ ‚Ññ1 –∞–±–æ ‚Ññ2. 
     * @param {number} player.hp - –ó–∞–ª–∏—à–æ–∫ –∂–∏—Ç—Ç—è –≥—Ä–∞–≤—Ü—è –≤—ñ–¥ 100% –¥–æ 0%.
     * @param {string} player.currentImg - –ü—É—Ç—å –¥–æ –∫–∞—Ä—Ç–∏–Ω–∫–∏ –≥–µ—Ä–æ—è.
     * @returns {HTMLElement} - –ì–æ—Ç–æ–≤–∏–π –≥—Ä–∞–≤–µ—Ü—å.
     */
    createPlayer = ({name, playerNumber, hp, currentImg}) => {
        const $player = createNewTag({
            tagName: 'div',
            className: 'player' + playerNumber, // 1|2.
        });
        
        const $progressbar = createNewTag({
            tagName: 'div',
            className: 'progressbar',
        });
        
        const $life = createNewTag({
            tagName: 'div',
            className: 'life',
        });    
        $life.style.width = hp + '%'; // life 100%.
        
        const $name = createNewTag({
            tagName: 'div',
            className: 'name',
            content: name,
        });
        
        const $character = createNewTag({
            tagName: 'div',
            className: 'character',
        });
        
        const $img = createNewTag({
            tagName: 'img',
            attrList: {
                src: currentImg, 
                alt: name,
            },            
        });
        
        $player.append($progressbar);
        $player.append($character);
        $progressbar.append($life);
        $progressbar.append($name);
        $character.append($img);
        
        return $player;
    }

    /**
     * –§—É–Ω–∫—Ü—ñ—è –¥–ª—è –¥–æ–¥–∞–≤–∞–Ω–Ω—è HTML –µ–ª–µ–º–µ–Ω—Ç—ñ–≤ –¥–æ –∞—Ä–µ–Ω–∏.
     * 
     * @param {HTMLElement} $elem - HTMLElement —è–∫–∏–π —Ç—Ä–µ–±–∞ –¥–æ–¥–∞—Ç–∏ –¥–æ –∞—Ä–µ–Ω–∏.
     */
    addingToArena = $elem => this.$arena.append($elem);

    /**
     * –§—É–Ω–∫—Ü—ñ—è –¥–ª—è –æ–±—Ä–æ–±–∫–∏ –ø–æ–¥—ñ—ó –ø—Ä–∏ –≤—ñ–¥–ø—Ä–∞–≤—Ü—ñ —Ñ–æ—Ä–º–∏ "control".
     * 
     * @param {object} player1 - –û–±'—î–∫—Ç –≥—Ä–∞–≤—Ü—è ‚Ññ1. 
     * @param {object} player2 - –û–±'—î–∫—Ç –≥—Ä–∞–≤—Ü—è ‚Ññ2. 
     */
    submitResult = (player1, player2) => {
        this.$form.addEventListener('submit', (e) => {
            e.preventDefault();   

            this.$btn.disabled = true; 
            this.play(player1, player2);

            clearGarbageTags('.sound-action-select');
            clearGarbageTags('.sound-action-hover');
        });
    }

    /**
     * –§—É–Ω–∫—Ü—ñ—è –¥–ª—è –ø–æ—Å–ª—ñ–¥–æ–≤–Ω–æ–≥–æ –≤–∏–∫–æ–Ω–∞–Ω–Ω—è –¥—ñ–π –ø—ñ–¥ —á–∞—Å –≥—Ä–∏.
     * 
     * @param {object} player1 - –û–±'—î–∫—Ç –≥—Ä–∞–≤—Ü—è ‚Ññ1. 
     * @param {object} player2 - –û–±'—î–∫—Ç –≥—Ä–∞–≤—Ü—è ‚Ññ2. 
     */
    async play(player1, player2) {
        await delay(0).then(() => this.hideControls());
        await delay(500).then(() => this.fight(player1, player2));
        await delay(4000).then(() => this.showResult(player1, player2));
    }

    /**
     * –§—É–Ω–∫—Ü—ñ—è –¥–ª—è —Å—Ç–≤–æ—Ä–µ–Ω–Ω—è –ø–∞—Ä–∞–º–µ—Ç—Ä—ñ–≤ –∞—Ç–∞–∫–∏ –≥—Ä–∞–≤—Ü—è (–∫–æ–º–ø'—é—Ç–µ—Ä–∞).
     * 
     * @returns {object} - –û–±'—î–∫—Ç –ø–∞—Ä–∞–º–µ—Ç—Ä—ñ–≤ –∞—Ç–∞–∫–∏ –≤–∏–¥—É: 
     * {
     *   hit: 'head|body|foot',      // - —É–¥–∞—Ä.
     *   defence: 'head|body|foot',  // - –∑–∞—Ö–∏—Å—Ç.
     *   value: 30                   // - —Å–∏–ª–∞ —É–¥–∞—Ä—É.
     * }
     */
    generateComputerPlayerAttackParams = () => {
        const hit = ATTACK[getRandom(ATTACK.length) - 1];
        const defence = ATTACK[getRandom(ATTACK.length) - 1];

        return {
            value: getRandom(HIT[hit]),
            hit,
            defence,
        }
    }

    /**
     * –§—É–Ω–∫—Ü—ñ—è –¥–ª—è —Å—Ç–≤–æ—Ä–µ–Ω–Ω—è –ø–∞—Ä–∞–º–µ—Ç—Ä—ñ–≤ –∞—Ç–∞–∫–∏ –≥—Ä–∞–≤—Ü—è (–∂–∏–≤–æ–≥–æ –≥—Ä–∞–≤—Ü—è).
     * 
     * @returns {object} - –û–±'—î–∫—Ç –ø–∞—Ä–∞–º–µ—Ç—Ä—ñ–≤ –∞—Ç–∞–∫–∏ –≤–∏–¥—É: 
     * {
     *   hit: 'head|body|foot',      // - —É–¥–∞—Ä.
     *   defence: 'head|body|foot',  // - –∑–∞—Ö–∏—Å—Ç.
     *   value: 30                   // - —Å–∏–ª–∞ —É–¥–∞—Ä—É.
     * }
     */
    generateRealPlayerAttackParams = () => {
        const attackParams = {};

        if (this.$form) {
            for (let item of this.$form) {
                if (item.checked && item.name === 'hit') {
                    attackParams.value = getRandom(HIT[item.value]); // –†–æ–∑–º—ñ—Ä –∑–∞–ø–æ–¥—ñ—è–Ω–æ—ó –≤—Ç—Ä–∞—Ç–∏ –∂–∏—Ç—Ç—è (—Å–∏–ª–∞ —É–¥–∞—Ä—É).
                    attackParams.hit = item.value;
                }
                
                if (item.checked && item.name === 'defence') {
                    attackParams.defence = item.value;
                }
                
                item.checked = false; // –û—á–∏—â–µ–Ω–Ω—è —Ñ–æ—Ä–º–∏.                
            }
        }

        return attackParams;
    }

    /**
     * –§—É–Ω–∫—Ü—ñ—è –¥–ª—è –∑–¥—ñ–π—Å–Ω–µ–Ω–Ω—è –∞—Ç–∞–∫–∏ –Ω–∞ –ø—Ä–æ—Ç–∏–≤–Ω–∏–∫–∞, —É—Å–ø—ñ—à–Ω–æ—ó –∞–±–æ –Ω—ñ.
     * 
     * @param {object[]} attacking - –ú–∞—Å–∏–≤ –∞—Ç–∞–∫—É—é—á–æ–≥–æ –≥—Ä–∞–≤—Ü—è.
     * @param {object} attacking[].attackingPlayer - –û–±'—î–∫—Ç –≥—Ä–∞–≤—Ü—è ‚Ññ1.
     * @param {object} attacking[].attackingParams - –û–±'—î–∫—Ç –ø–∞—Ä–∞–º–µ—Ç—Ä—ñ–≤ –∞—Ç–∞–∫–∏.
     * @param {object[]} defensive - –ú–∞—Å–∏–≤ –æ–±–æ—Ä–æ–Ω—è—é—á–æ–≥–æ –≥—Ä–∞–≤—Ü—è.
     * @param {object} defensive[].defensivePlayer - –û–±'—î–∫—Ç –≥—Ä–∞–≤—Ü—è ‚Ññ2.
     * @param {object} defensive[].defensiveParams - –û–±'—î–∫—Ç –ø–∞—Ä–∞–º–µ—Ç—Ä—ñ–≤ –∞—Ç–∞–∫–∏.
     */
    makeAttack = ([attackingPlayer, attackingParams], [defensivePlayer, defensiveParams]) => {
        if (attackingPlayer.hp > 0 && defensivePlayer.hp > 0) {
            const attackingPlayerHit = attackingParams.hit;
            const defensivePlayerDefence = defensiveParams.defence;

            this.animationAttackingPlayer(attackingPlayer, attackingPlayerHit);            
            this.animationDefensivePlayer(defensivePlayer, defensivePlayerDefence);

            setTimeout(() => { // –ó–∞—Ç—Ä–∏–º–∫–∞ —Ç—É—Ç —â–æ–± –∑–∞–ª–∏—à–æ–∫ –∂–∏—Ç—Ç—è –Ω–µ –∑–º–µ–Ω—à—É–≤–∞–≤—Å—è –¥–æ –Ω–∞–Ω–µ—Å–µ–Ω–Ω—è —É–¥–∞—Ä—É.  
                if (!(attackingPlayerHit === defensivePlayerDefence || attackingPlayerHit !== defensivePlayerDefence && defensivePlayerDefence === 'special')) { // –£—Å–ø—ñ—à–Ω–∞ –∞—Ç–∞–∫–∞.
                    const damage = attackingParams.value; // –ù–∞–Ω–µ—Å–µ–Ω–∞ —à–∫–æ–¥–∞ (—Å–∏–ª–∞ —É–¥–∞—Ä—É –∞—Ç–∞–∫—É—é—á–æ–≥–æ).          

                    defensivePlayer.changeHP(damage); // –ó–º—ñ–Ω–∞ –∑–∞–ª–∏—à–∫—É –∂–∏—Ç—Ç—è —É –æ–±–æ—Ä–æ–Ω—è—é—á–æ–≥–æ.         
                                        
                    createSoundTrack({
                        className: 'sound-fight',
                        src: soundHit,
                    });

                    if (defensivePlayer.hp <= 0) {
                        this[`$player${defensivePlayer.playerNumber}`].querySelector('.character img').src = defensivePlayer.dizzy; // –ü–æ–ø–ª–∏–≤ (—Å–º–µ—Ä—Ç—å).
                        
                        createSoundTrack({
                            className: 'sound-finish',
                            src: {m: soundDamage3M, g: soundDamage3G},
                            gender: defensivePlayer.name,
                        });       
                    } else if (defensivePlayer.hp > 0 && damage >= 10) {
                        this[`$player${defensivePlayer.playerNumber}`].querySelector('.character img').src = defensivePlayer.dizzy; // –ü–µ—Ä–µ–Ω—ñ—Å –¥—É–∂–µ —Å–∏–ª—å–Ω–∏–π —É–¥–∞—Ä.
                        
                        createSoundTrack({
                            className: 'sound-fight',
                            src: {m: soundDamage2M, g: soundDamage2G},
                            gender: defensivePlayer.name,
                        });

                        setTimeout(() => {
                            this[`$player${defensivePlayer.playerNumber}`].querySelector('.character img').src = defensivePlayer.stance;
                        }, 1100);
                    } else if (defensivePlayer.hp > 0 && damage < 10) {
                        this[`$player${defensivePlayer.playerNumber}`].querySelector('.character img').src = defensivePlayer.slipping; // –ü–µ—Ä–µ–Ω—ñ—Å —É–¥–∞—Ä.
                        
                        createSoundTrack({
                            className: 'sound-fight',
                            src: {m: soundDamageM, g: soundDamageG},
                            gender: defensivePlayer.name,
                        });    
                        
                        setTimeout(() => {
                            this[`$player${defensivePlayer.playerNumber}`].querySelector('.character img').src = defensivePlayer.stance;
                        }, 1100);
                    }

                    defensivePlayer.renderHP(); // –û–Ω–æ–≤–ª–µ–Ω–Ω—è –∑–∞–ª–∏—à–∫—É –∂–∏—Ç—Ç—è –æ–±–æ—Ä–æ–Ω—è—é—á–æ–≥–æ –Ω–∞ —Ç–∞–±–ª–æ.
                    
                    this.logs.generateLogs({type: 'hit', attackingPlayer, defensivePlayer, damage}); // –û–ø–∏—Å –¥—ñ–π —â–æ –≤—ñ–¥–±—É–ª–∏—Å—è –ø—ñ–¥ —á–∞—Å –≤–¥–∞–ª–æ—ó –∞—Ç–∞–∫–∏.
                } else { // –ê—Ç–∞–∫–∞ –Ω–µ –≤–¥–∞–ª–∞—Å—è.
                    this.logs.generateLogs({type: 'defence', attackingPlayer, defensivePlayer}); // –û–ø–∏—Å –¥—ñ–π —â–æ –≤—ñ–¥–±—É–ª–∏—Å—è –ø—ñ–¥ —á–∞—Å –Ω–µ –≤–¥–∞–ª–æ—ó –∞—Ç–∞–∫–∏.

                    createSoundTrack({
                        className: 'sound-fight',
                        src: soundHit2,
                    });
                    
                    createSoundTrack({
                        className: 'sound-fight',
                        src: {m: soundDefenceM, g: soundDefenceG},
                        gender: defensivePlayer.name,
                    });
                    
                    setTimeout(() => { 
                        this[`$player${defensivePlayer.playerNumber}`].querySelector('.character img').src = defensivePlayer.stance;
                    }, 1000);
                }             
            }, 2000);
        } 
    }

    /**
     * –§—É–Ω–∫—Ü—ñ—è –¥–ª—è –∞–≤—Ç–æ–º–∞—Ç–∏—á–Ω–æ–≥–æ –≤–∏–±–æ—Ä—É —É–¥–∞—Ä—É —á–∏ –∑–∞—Ö–∏—Å—Ç—É –∑ –º–æ–∂–ª–∏–≤–∏—Ö –≤–∞—Ä—ñ–∞–Ω—Ç—ñ–≤.
     * 
     * @param {object} player - –û–±'—î–∫—Ç –≥–µ—Ä–æ—è.
     * @param {string} type - –¢–∏–ø —É–¥–∞—Ä—É —á–∏ –∑–∞—Ö–∏—Å—Ç—É.
     * @returns {string} - –¢–∏–ø —É–¥–∞—Ä—É —á–∏ –∑–∞—Ö–∏—Å—Ç—É.
     */
    createTypeVariant = (player, type, action) => {
        const hitPlayerName= player.name.toLowerCase().replace(/-(\d)+$/gim, ''); // –í–∏–¥–∞–ª—è—î–º–æ –∑–∞–∫—ñ–Ω—á–µ–Ω–Ω—è -1|-2 –∑ —ñ–º–µ–Ω—ñ –≥–µ—Ä–æ—è.
        const typeVariants = (action === 'hit') ? HITTYPE[type] : DEFENCETYPE[type]; // –ú–∞—Å—Å–∏–≤ —ñ–∑ –º–æ–∂–ª–∏–≤–∏—Ö –≤–∞—Ä—ñ–∞–Ω—Ç—ñ–≤ —è–∫ –∑–¥—ñ–π—Å–Ω–∏—Ç–∏ —É–¥–∞—Ä –∞–±–æ –∑–∞—Ö–∏—Å—Ç.
        const typeVariantsCount = typeVariants.length; // –î–æ—Å—Ç—É–ø–Ω–∞ –∫—ñ–ª—å–∫—ñ—Å—Ç—å –º–æ–∂–ª–∏–≤–∏—Ö –≤–∞—Ä—ñ–∞–Ω—Ç—ñ–≤ –∑–¥—ñ–π—Å–Ω–µ–Ω–Ω—è —É–¥–∞—Ä—É –∞–±–æ –∑–∞—Ö–∏—Å—Ç—É.
        let typeVariant = typeVariants[getRandom(typeVariantsCount) - 1]; // –í–∏–ø–∞–¥–∫–æ–≤–∏–π –≤–∞—Ä—ñ–∞–Ω—Ç.

        if (!ANIMATE[hitPlayerName].some(item => item === typeVariant)) {            
            typeVariant = this.createTypeVariant(player, type, action);
        }

        return typeVariant;
    }

    /**
     * –§—É–Ω–∫—Ü—ñ—è –¥–ª—è –∑–¥—ñ–π—Å–Ω–µ–Ω–Ω—è –∞–ª–≥–æ—Ä–∏—Ç–º—É –∞–Ω—ñ–º–∞—Ü—ñ—ó –≥–µ—Ä–æ—è —â–æ –æ–±–æ—Ä–æ–Ω—è—î—Ç—å—Å—è, —Ç–∏–ø –∑–∞—Ö–∏—Å—Ç—É - —Å—É–ø–µ—Ä –∑–∞—Ö–∏—Å—Ç, –¥—ñ—î –≤—ñ–¥ –≤—Å—ñ—Ö –≤–∏–¥—ñ–≤ —É–¥–∞—Ä—ñ–≤.
     * 
     * @param {object} params - –û–±'—î–∫—Ç –∑ –¥–∞–Ω–∏–º–∏ –ø—Ä–æ —Ç–∏–ø –∑–∞—Ö–∏—Å—Ç—É. 
     * @param {object} params.defensivePlayer - –û–±'—î–∫—Ç –≥–µ—Ä–æ—è —â–æ –æ–±–æ—Ä–æ–Ω—è—î—Ç—å—Å—è.. 
     * @param {string} params.defenceMethodName - –ù–∞–∑–≤–∞ –≤–ª–∞—Å—Ç–∏–≤–æ—Å—Ç—ñ –æ–±'—î–∫—Ç–∞ (–∫–æ–º–∞–Ω–¥–∞ –∞–Ω—ñ–º–∞—Ü—ñ—ó). 
     * @param {HTMLElement} params.$character - –ï–ª–µ–º–µ–Ω—Ç –≥–µ—Ä–æ—è. 
     * @param {HTMLElement} params.$characterImg - –ï–ª–µ–º–µ–Ω—Ç –∫–∞—Ä—Ç–∏–Ω–∫–∞ –≥–µ—Ä–æ—è. 
     */
    async animationDefensiveTypeSpecial({defensivePlayer, defenceMethodName, $character, $characterImg}) {
        await delay(0).then(() => {
            //$character.classList.add('walk-back'); // –í—ñ–¥–±—É–≤–∞—î—Ç—å—Å—è –ø–µ—Ä–µ–º—ñ—â–µ–Ω–Ω—è –≥–µ—Ä–æ—è –ø–æ –∞—Ä–µ–Ω—ñ –≤—ñ–¥—Ö–æ–¥–µ –Ω–∞–∑–∞–¥. 
            this.movePlayerOnArena(defensivePlayer, $character, 'walk-back'); // –í—ñ–¥–±—É–≤–∞—î—Ç—å—Å—è –ø–µ—Ä–µ–º—ñ—â–µ–Ω–Ω—è –≥–µ—Ä–æ—è –ø–æ –∞—Ä–µ–Ω—ñ –≤—ñ–¥—Ö–æ–¥–µ –Ω–∞–∑–∞–¥. 
            $characterImg.src = defensivePlayer[defenceMethodName]; // –ü—ñ–¥—Å—Ç–∞–≤–ª—è—î–º–æ –∞–Ω—ñ–º–∞—Ü—ñ—é –≥–µ—Ä–æ–π –≤—ñ–¥—Ö–æ–¥–µ –Ω–∞–∑–∞–¥.

            createSoundTrack({
                className: 'sound-defensive-running',
                src: soundRunning,
                loop: true,
            });
        });        
        await delay(2000).then(() => {
            //$character.classList.remove('walk-back'); // –í—ñ–¥–±—É–≤–∞—î—Ç—å—Å—è –ø–µ—Ä–µ–º—ñ—â–µ–Ω–Ω—è –≥–µ—Ä–æ—è –ø–æ –∞—Ä–µ–Ω—ñ –ø–æ–≤–µ—Ä—Ç–∞—î—Ç—å—Å—è –Ω–∞ —Å–≤–æ—î –º—ñ—Å—Ü–µ.       
            this.movePlayerOnArena(defensivePlayer, $character, 'back'); // –í—ñ–¥–±—É–≤–∞—î—Ç—å—Å—è –ø–µ—Ä–µ–º—ñ—â–µ–Ω–Ω—è –≥–µ—Ä–æ—è –ø–æ –∞—Ä–µ–Ω—ñ –ø–æ–≤–µ—Ä—Ç–∞—î—Ç—å—Å—è –Ω–∞ —Å–≤–æ—î –º—ñ—Å—Ü–µ.       
            $characterImg.src = defensivePlayer.walk; // –ü—ñ–¥—Å—Ç–∞–≤–ª—è—î–º–æ –∞–Ω—ñ–º–∞—Ü—ñ—é –≥–µ—Ä–æ–π –π–¥–µ –≤–ø–µ—Ä–µ–¥.
        }); 
        await delay(500).then(() => {
            clearGarbageTags('.sound-defensive-running');

            $characterImg.src = defensivePlayer.stance; // –ü—ñ–¥—Å—Ç–∞–≤–ª—è—î–º–æ –∞–Ω—ñ–º–∞—Ü—ñ—é —Å—Ç—ñ–π–∫–∞.
        }); 
    }
    
    /**
     * –§—É–Ω–∫—Ü—ñ—è –¥–ª—è –∑–¥—ñ–π—Å–Ω–µ–Ω–Ω—è –∞–ª–≥–æ—Ä–∏—Ç–º—É –∞–Ω—ñ–º–∞—Ü—ñ—ó –≥–µ—Ä–æ—è —â–æ –æ–±–æ—Ä–æ–Ω—è—î—Ç—å—Å—è, —Ç–∏–ø –∑–∞—Ö–∏—Å—Ç—É - —Å–∫–∞—á–æ–∫ –≤–≥–æ—Ä—É.
     * 
     * @param {object} params - –û–±'—î–∫—Ç –∑ –¥–∞–Ω–∏–º–∏ –ø—Ä–æ —Ç–∏–ø –∑–∞—Ö–∏—Å—Ç—É. 
     * @param {object} params.defensivePlayer - –û–±'—î–∫—Ç –≥–µ—Ä–æ—è —â–æ –æ–±–æ—Ä–æ–Ω—è—î—Ç—å—Å—è.. 
     * @param {string} params.defenceMethodName - –ù–∞–∑–≤–∞ –≤–ª–∞—Å—Ç–∏–≤–æ—Å—Ç—ñ –æ–±'—î–∫—Ç–∞ (–∫–æ–º–∞–Ω–¥–∞ –∞–Ω—ñ–º–∞—Ü—ñ—ó). 
     * @param {HTMLElement} params.$character - –ï–ª–µ–º–µ–Ω—Ç –≥–µ—Ä–æ—è. 
     * @param {HTMLElement} params.$characterImg - –ï–ª–µ–º–µ–Ω—Ç –∫–∞—Ä—Ç–∏–Ω–∫–∞ –≥–µ—Ä–æ—è. 
     */
    async animationDefensiveTypeJumpToTop({defensivePlayer, defenceMethodName, $character, $characterImg}) {
        await delay(0).then(() => $characterImg.src = defensivePlayer[defenceMethodName]); // –ü—ñ–¥—Å—Ç–∞–≤–ª—è—î–º–æ –∞–Ω—ñ–º–∞—Ü—ñ—é –≥–µ—Ä–æ–π –ø—ñ–¥—Å–∫–∞–∫—É—î –≤–≥–æ—Ä—É.
        await delay(300).then(() => $character.classList.add('jump-to-top')); // –í—ñ–¥–±—É–≤–∞—î—Ç—å—Å—è –ø–µ—Ä–µ–º—ñ—â–µ–Ω–Ω—è –≥–µ—Ä–æ—è –ø–æ –∞—Ä–µ–Ω—ñ –ø—ñ–¥—Å–∫–∞–∫—É—î –≤–≥–æ—Ä—É.        
        await delay(2000).then(() => $character.classList.remove('jump-to-top')); // –í—ñ–¥–±—É–≤–∞—î—Ç—å—Å—è –ø–µ—Ä–µ–º—ñ—â–µ–Ω–Ω—è –≥–µ—Ä–æ—è –ø–æ –∞—Ä–µ–Ω—ñ –ø–æ–≤–µ—Ä—Ç–∞—î—Ç—å—Å—è –Ω–∞ —Å–≤–æ—î –º—ñ—Å—Ü–µ.        
    }
    
    /**
     * –§—É–Ω–∫—Ü—ñ—è –¥–ª—è –∑–¥—ñ–π—Å–Ω–µ–Ω–Ω—è –∞–ª–≥–æ—Ä–∏—Ç–º—É –∞–Ω—ñ–º–∞—Ü—ñ—ó –≥–µ—Ä–æ—è —â–æ –æ–±–æ—Ä–æ–Ω—è—î—Ç—å—Å—è, —Ç–∏–ø –∑–∞—Ö–∏—Å—Ç—É - –∑–≤–∏—á–∞–π–Ω–∞ –æ–±–æ—Ä–æ–Ω–∞.
     * 
     * @param {object} params - –û–±'—î–∫—Ç –∑ –¥–∞–Ω–∏–º–∏ –ø—Ä–æ —Ç–∏–ø –∑–∞—Ö–∏—Å—Ç—É. 
     * @param {object} params.defensivePlayer - –û–±'—î–∫—Ç –≥–µ—Ä–æ—è —â–æ –æ–±–æ—Ä–æ–Ω—è—î—Ç—å—Å—è.. 
     * @param {string} params.defenceMethodName - –ù–∞–∑–≤–∞ –≤–ª–∞—Å—Ç–∏–≤–æ—Å—Ç—ñ –æ–±'—î–∫—Ç–∞ (–∫–æ–º–∞–Ω–¥–∞ –∞–Ω—ñ–º–∞—Ü—ñ—ó). 
     * @param {HTMLElement} params.$characterImg - –ï–ª–µ–º–µ–Ω—Ç –∫–∞—Ä—Ç–∏–Ω–∫–∞ –≥–µ—Ä–æ—è. 
     */
    async animationDefensiveTypeDefault({defensivePlayer, defenceMethodName, $characterImg}) {
        await delay(0).then(() => $characterImg.src = defensivePlayer[defenceMethodName]); // –ü—ñ–¥—Å—Ç–∞–≤–ª—è—î–º–æ –∞–Ω—ñ–º–∞—Ü—ñ—é.
    }

    /**
     * –§—É–Ω–∫—Ü—ñ—è –¥–ª—è –∑–¥—ñ–π—Å–Ω–µ–Ω–Ω—è –∞–ª–≥–æ—Ä–∏—Ç–º—É –∞–Ω—ñ–º–∞—Ü—ñ—ó –≥–µ—Ä–æ—è —â–æ –æ–±–æ—Ä–æ–Ω—è—î—Ç—å—Å—è.
     * 
     * @param {object} defensivePlayer - –û–±'—î–∫—Ç –≥–µ—Ä–æ—è —â–æ –æ–±–æ—Ä–æ–Ω—è—î—Ç—å—Å—è.
     * @param {string} defenceType - –¢–∏–ø –∑–∞—Ö–∏—Å—Ç—É.
     */
    animationDefensivePlayer = (defensivePlayer, defenceType) => {    
        const defenceTypeVariant = this.createTypeVariant(defensivePlayer, defenceType, 'defence'); // –í–∏–ø–∞–¥–∫–æ–≤–∏–π –≤–∞—Ä—ñ–∞–Ω—Ç –¥—ñ—ó.                
        const defenceMethodName = camelize(defenceTypeVariant); // –ù–∞–∑–≤–∞ –≤–ª–∞—Å—Ç–∏–≤–æ—Å—Ç—ñ –æ–±'—î–∫—Ç–∞ (–∫–æ–º–∞–Ω–¥–∞ –∞–Ω—ñ–º–∞—Ü—ñ—ó).
        const $player = this[`$player${defensivePlayer.playerNumber}`]; // –ï–ª–µ–º–µ–Ω—Ç –≥—Ä–∞–≤–µ—Ü—å.
        const $character = $player.querySelector('.character'); // –ï–ª–µ–º–µ–Ω—Ç –≥–µ—Ä–æ—è.
        const $characterImg = $character.querySelector('img'); // –ï–ª–µ–º–µ–Ω—Ç –∫–∞—Ä—Ç–∏–Ω–∫–∞ –≥–µ—Ä–æ—è.

        $player.classList.remove('priority');

        switch (defenceTypeVariant) {
            case 'walk-back':
                this.animationDefensiveTypeSpecial({defensivePlayer, defenceMethodName, $character, $characterImg});
                
                break;
            case 'jump-to-top':
                this.animationDefensiveTypeJumpToTop({defensivePlayer, defenceMethodName, $character, $characterImg});

                break;        
            default:
                this.animationDefensiveTypeDefault({defensivePlayer, defenceMethodName, $characterImg});
                
                break;
        }

        clearGarbageTags('.sound-fight');
    }

    /**
     * –§—É–Ω–∫—Ü—ñ—è –¥–ª—è –∑–¥—ñ–π—Å–Ω–µ–Ω–Ω—è –∞–ª–≥–æ—Ä–∏—Ç–º—É –∞–Ω—ñ–º–∞—Ü—ñ—ó –≥–µ—Ä–æ—è —â–æ –∞—Ç–∞–∫—É—î, —Ç–∏–ø –∞—Ç–∞–∫–∏ - —Å—É–ø–µ—Ä —É–¥–∞—Ä. 
     * 
     * @param {object} params - –û–±'—î–∫—Ç –∑ –¥–∞–Ω–∏–º–∏ –¥–ª—è –∑–¥—ñ–π—Å–Ω–µ–Ω–Ω—è –∞—Ç–∞–∫–∏.
     * @param {object} params.attackingPlayer - –û–±'—î–∫—Ç –≥—Ä–∞–≤—Ü—è —â–æ –∞—Ç–∞–∫—É—î.
     * @param {string} params.hitMethodName - –í–∏–¥ —É–¥–∞—Ä—É –¥–ª—è –∞—Ç–∞–∫–∏.
     * @param {HTMLElement} params.$character - –ï–ª–µ–º–µ–Ω—Ç –≥—Ä–∞–≤–µ—Ü—å –Ω–∞ –∞—Ä–µ–Ω—ñ.
     * @param {HTMLElement} params.$characterImg - –ó–æ–±—Ä–∞–∂–µ–Ω–Ω—è –≥—Ä–∞–≤—Ü—è –Ω–∞ –∞—Ä–µ–Ω—ñ. 
     */
    async animationAttackingTypeSpecial({attackingPlayer, hitMethodName, $character, $characterImg}) {
        await delay(0).then(() => {
            createSoundTrack({
                className: 'sound-fight',
                src: {m: soundAttackM, g: soundAttackG},
                gender: attackingPlayer.name,
            }); // –ó–≤—É–∫ –∞—Ç–∞–∫–∏ –∫—ñ—è.
        }); // –ù–∞–Ω–æ—Å—è—Ç—å—Å—è —É–¥–∞—Ä–∏ –ø—Ä–æ—Ç–∏–≤–Ω–∏–∫—É.                                
        await delay(0).then(() => $characterImg.src = attackingPlayer[hitMethodName]); // –ù–∞–Ω–æ—Å–∏—Ç—å—Å—è —Å—É–ø–µ—Ä —É–¥–∞—Ä –ø—Ä–æ—Ç–∏–≤–Ω–∏–∫—É.                                            
        await delay(500).then(() => {            
            const $fly = createNewTag({
                tagName: 'img',
                className: 'player' + attackingPlayer.playerNumber + '-fly', // 1|2.
                attrList: {
                    src: attackingPlayer.specialFly, 
                    alt: attackingPlayer.name + 'Super Hit',
                },            
            });            

            $character.append($fly);
            this.getFlyDestanceForPlayerHit(attackingPlayer);
            //$character.classList.add('special')
        }); // –í—ñ–¥–±—É–≤–∞—î—Ç—å—Å—è –ø–µ—Ä–µ–º—ñ—â–µ–Ω–Ω—è –≤–æ–≥–Ω—é –≥–µ—Ä–æ—è –ø–æ –∞—Ä–µ–Ω—ñ.        
        await delay(2500).then(() => {
            document.querySelector('.player' + attackingPlayer.playerNumber + '-fly').src = '';
            document.querySelector('.player' + attackingPlayer.playerNumber + '-fly').remove();
            $characterImg.src = attackingPlayer.stance; // –ü—ñ–¥—Å—Ç–∞–≤–ª—è—î–º–æ –∞–Ω—ñ–º–∞—Ü—ñ—é —Å—Ç—ñ–π–∫–∞.
        });
    }

    /**
     * –§—É–Ω–∫—Ü—ñ—è –¥–ª—è –∑–¥—ñ–π—Å–Ω–µ–Ω–Ω—è –ø–µ—Ä–µ–º—ñ—â–µ–Ω–Ω—è –æ–±'—î–∫—Ç—É (—Å–Ω–∞—Ä—è–¥—É) —â–æ –≤–∏–ø—É—Å—Ç–∏–≤ –≥–µ—Ä–æ–π –∑–¥—ñ–π—Å–Ω–∏–≤—à–∏ —Å—É–ø–µ—Ä —É–¥–∞—Ä.
     * 
     * @param {object} attackingPlayer - –æ–±'—î–∫—Ç –≥–µ—Ä–æ—è —â–æ –∞—Ç–∞–∫—É—î.
     */
    getFlyDestanceForPlayerHit = (attackingPlayer) => {        
        const arenaCurrentCoordinates = this.$arena.getBoundingClientRect();

        if (attackingPlayer.playerNumber === 1) {
            document.querySelector('.player1-fly').style.left = arenaCurrentCoordinates.width + 'px'; 
        } else {
            document.querySelector('.player2-fly').style.right = arenaCurrentCoordinates.width + 'px';
        }
    }

    /**
     * –§—É–Ω–∫—Ü—ñ—è –¥–ª—è –∑–¥—ñ–π—Å–Ω–µ–Ω–Ω—è –ø–µ—Ä–µ–º—ñ—â–µ–Ω–Ω—è –≥–µ—Ä–æ—è –ø–æ –∞—Ä–µ–Ω—ñ.
     * 
     * @param {object} player - –û–±'—î–∫—Ç –≥—Ä–∞–≤—Ü—è —â–æ –º–∞—î –ø–µ—Ä–µ–º—ñ—Å—Ç–∏—Ç–∏—Å—è.
     * @param {HTMLElement} $character - –ï–ª–µ–º–µ–Ω—Ç –≥—Ä–∞–≤–µ—Ü—å –Ω–∞ –∞—Ä–µ–Ω—ñ.
     * @param {string} pointer - –¢–æ—á–∫–∞ –≤ —è–∫—É –º–∞—î –ø–µ—Ä–µ–º—ñ—Å—Ç–∏—Ç–∏—Å—è –≥–µ—Ä–æ–π.
     */
    movePlayerOnArena = (player, $character, pointer) => {
        const arenaCurrentCoordinates = this.$arena.getBoundingClientRect();
        const player1CurrentCoordinates = document.querySelector('.player1 .character img').getBoundingClientRect();
        const player2CurrentCoordinates = document.querySelector('.player2 .character img').getBoundingClientRect();    
        const distance = player2CurrentCoordinates.left - player1CurrentCoordinates.right;    
        const player1Half = player1CurrentCoordinates.width / 2;   
        const player2Half = player2CurrentCoordinates.width / 2;   
        const player1Indent = player1CurrentCoordinates.left - arenaCurrentCoordinates.left; 
        const player2Indent = arenaCurrentCoordinates.right - player2CurrentCoordinates.right; 
        
        switch (pointer) {
            case 'run':
                if (player.playerNumber === 1) { 
                    $character.style.left = player1Indent + distance + player2Half + 'px'; 
                } else {
                    $character.style.right = player2Indent + distance + player1Half + 'px';            
                }

                break;                
            case 'back':
                if (player.playerNumber === 1) {
                    $character.style.left = 20 + '%'; 
                } else {
                    $character.style.right = 20 + '%'; 
                }

                break;
            case 'walk-back':
                if (player.playerNumber === 1) {
                    $character.style.left = - player1Half + 'px'; 
                } else {
                    $character.style.right = - player2Half + 'px'; 
                }

                break;
        }        
    }   
    
    /**
     * –§—É–Ω–∫—Ü—ñ—è –¥–ª—è –∑–¥—ñ–π—Å–Ω–µ–Ω–Ω—è –∞–ª–≥–æ—Ä–∏—Ç–º—É –∞–Ω—ñ–º–∞—Ü—ñ—ó –≥–µ—Ä–æ—è —â–æ –∞—Ç–∞–∫—É—î, —Ç–∏–ø –∞—Ç–∞–∫–∏ - –∑–≤–∏—á–∞–π–Ω–∞ –∞—Ç–∞–∫–∞.
     * 
     * @param {object} params - –û–±'—î–∫—Ç –∑ –¥–∞–Ω–∏–º–∏ –¥–ª—è –∑–¥—ñ–π—Å–Ω–µ–Ω–Ω—è –∞—Ç–∞–∫–∏.
     * @param {object} params.attackingPlayer - –û–±'—î–∫—Ç –≥—Ä–∞–≤—Ü—è —â–æ –∞—Ç–∞–∫—É—î.
     * @param {string} params.hitMethodName - –í–∏–¥ —É–¥–∞—Ä—É –¥–ª—è –∞—Ç–∞–∫–∏.
     * @param {HTMLElement} params.$character - –ï–ª–µ–º–µ–Ω—Ç –≥—Ä–∞–≤–µ—Ü—å –Ω–∞ –∞—Ä–µ–Ω—ñ.
     * @param {HTMLElement} params.$characterImg - –ó–æ–±—Ä–∞–∂–µ–Ω–Ω—è –≥—Ä–∞–≤—Ü—è –Ω–∞ –∞—Ä–µ–Ω—ñ.
     */
    async animationAttackingTypeDefault({attackingPlayer, hitMethodName, $character, $characterImg}) {
        await delay(0).then(() => {
            $characterImg.src = attackingPlayer.run; // –ü—ñ–¥—Å—Ç–∞–≤–ª—è—î–º–æ –∞–Ω—ñ–º–∞—Ü—ñ—é –±—ñ–≥ –≤–ø–µ—Ä–µ–¥.
            this.movePlayerOnArena(attackingPlayer, $character, 'run'); // –í—ñ–¥–±—É–≤–∞—î—Ç—å—Å—è –ø–µ—Ä–µ–º—ñ—â–µ–Ω–Ω—è –≥–µ—Ä–æ—è –ø–æ –∞—Ä–µ–Ω—ñ.   

            createSoundTrack({
                className: 'sound-attacking-running',
                src: soundRunning,
                loop: true,
            }); // –ó–≤—É–∫ –±—ñ–≥.                
        }); 
        await delay(600).then(() => {
            createSoundTrack({
                className: 'sound-fight',
                src: {m: soundAttackM, g: soundAttackG},
                gender: attackingPlayer.name,
            }); // –ó–≤—É–∫ –∞—Ç–∞–∫–∏ –∫—ñ—è.  
        }); 
        await delay(500).then(() => {
            clearGarbageTags('.sound-attacking-running');        
            $characterImg.src = attackingPlayer[hitMethodName]; // –ù–∞–Ω–æ—Å—è—Ç—å—Å—è —É–¥–∞—Ä–∏ –ø—Ä–æ—Ç–∏–≤–Ω–∏–∫—É.
        });                 
        await delay(1000).then(() => {      
            $characterImg.src = attackingPlayer.run; // –ü—ñ–¥—Å—Ç–∞–≤–ª—è—î–º–æ –∞–Ω—ñ–º–∞—Ü—ñ—é –±—ñ–≥ –≤–ø–µ—Ä–µ–¥.        
            $character.classList.add('run-back'); // –í—ñ–¥–±—É–≤–∞—î—Ç—å—Å—è –ø–µ—Ä–µ–º—ñ—â–µ–Ω–Ω—è –≥–µ—Ä–æ—è –ø–æ –∞—Ä–µ–Ω—ñ –Ω–∞–∑–∞–¥.   
            this.movePlayerOnArena(attackingPlayer, $character, 'back');

            createSoundTrack({
                className: 'sound-attacking-running',
                src: soundRunning,
                loop: true,
            }); // –ó–≤—É–∫ –±—ñ–≥.
        });              
        await delay(1000).then(() => {
            $character.classList.remove('run-back'); // –û—á–∏—â–∞—î—Ç—å—Å—è –∫–ª–∞—Å —â–æ –∑–¥—ñ–π—Å–Ω—é—î –±—ñ–≥.                
            
            clearGarbageTags('.sound-attacking-running');        
        }); 
        await delay(0).then(() => $characterImg.src = attackingPlayer.stance); // –ü—ñ–¥—Å—Ç–∞–≤–ª—è—î–º–æ –∞–Ω—ñ–º–∞—Ü—ñ—é —Å—Ç—ñ–π–∫–∞.
    }
    
    /**
     * –§—É–Ω–∫—Ü—ñ—è –¥–ª—è –∑–¥—ñ–π—Å–Ω–µ–Ω–Ω—è –∞–ª–≥–æ—Ä–∏—Ç–º—É –∞–Ω—ñ–º–∞—Ü—ñ—ó –≥–µ—Ä–æ—è —â–æ –∞—Ç–∞–∫—É—î.
     * 
     * @param {object} attackingPlayer - –û–±'—î–∫—Ç –∞—Ç–∞–∫—É—é—á–æ–≥–æ –≥–µ—Ä–æ—è.
     * @param {string} hitType - –¢–∏–ø —É–¥–∞—Ä—É.
     */
    animationAttackingPlayer = (attackingPlayer, hitType) => {        
        const hitTypeVariant = this.createTypeVariant(attackingPlayer, hitType, 'hit'); // –í–∏–ø–∞–¥–∫–æ–≤–∏–π –≤–∞—Ä—ñ–∞–Ω—Ç.            
        const hitMethodName = camelize(hitTypeVariant); // –ù–∞–∑–≤–∞ –≤–ª–∞—Å—Ç–∏–≤–æ—Å—Ç—ñ –æ–±'—î–∫—Ç–∞ (–∫–æ–º–∞–Ω–¥–∞ –∞–Ω—ñ–º–∞—Ü—ñ—ó).        
        const $player = this[`$player${attackingPlayer.playerNumber}`];
        const $character = $player.querySelector('.character');
        const $characterImg = $character.querySelector('img');

        $player.classList.add('priority');

        switch (hitTypeVariant) {        
            case 'special':
                this.animationAttackingTypeSpecial({attackingPlayer, hitMethodName, $character, $characterImg});

                break;        
            default:
                this.animationAttackingTypeDefault({attackingPlayer, hitMethodName, $character, $characterImg});

                break;
        }        
    }

    /**
     * –§—É–Ω–∫—Ü—ñ—è –¥–ª—è –∑–¥—ñ–π—Å–Ω–µ–Ω–Ω—è –ø–æ—î–¥–∏–Ω–∫—É.
     * 
     * @param {object} player1 - –û–±–µ–∫—Ç –≥—Ä–∞–≤—Ü—è ‚Ññ1. 
     * @param {object} player2 - –û–±–µ–∫—Ç –≥—Ä–∞–≤—Ü—è ‚Ññ2.
     */
    async fight(player1, player2) {
        const computerPlayerAttack = this.generateComputerPlayerAttackParams(); // –°—Ç–≤–æ—Ä–µ–Ω–Ω—è –ø–∞—Ä–∞–º–µ—Ç—Ä—ñ–≤ –∞—Ç–∞–∫–∏ (–∂–∏–≤–∏–π –≥—Ä–∞–≤–µ—Ü—å).
        const realPlayerAttack = this.generateRealPlayerAttackParams(); // –°—Ç–≤–æ—Ä–µ–Ω–Ω—è –ø–∞—Ä–∞–º–µ—Ç—Ä—ñ–≤ –∞—Ç–∞–∫–∏ (–∫–æ–º–ø'—é—Ç–µ—Ä).
        
        await delay(0).then(() => this.makeAttack([player1, realPlayerAttack], [player2, computerPlayerAttack])); // –ê—Ç–∞–∫—É—î –≥—Ä–∞–≤–µ—Ü—å ‚Ññ1, –≥—Ä–∞–≤–µ—Ü—å ‚Ññ2 –∑–∞—Ö–∏—â–∞—î—Ç—å—Å—è.
        await delay(4000).then(() => this.makeAttack([player2, computerPlayerAttack], [player1, realPlayerAttack])); // –ê—Ç–∞–∫—É—î –≥—Ä–∞–≤–µ—Ü—å ‚Ññ2, –≥—Ä–∞–≤–µ—Ü—å ‚Ññ1 –∑–∞—Ö–∏—â–∞—î—Ç—å—Å—è.
    }

    /**
     * –§—É–Ω–∫—Ü—ñ—è –¥–ª—è —Å—Ç–≤–æ—Ä–µ–Ω–Ω—è —Ñ—ñ–Ω–∞–ª—å–Ω–æ–≥–æ –ø–æ–≤—ñ–¥–æ–º–ª–µ–Ω–Ω—è (–∑–∞–≥–æ–ª–æ–≤–∫–∞).
     * 
     * @param {string|undefined} [championName] - –ù–µ –æ–±–æ–≤'—è–∑–∫–æ–≤–∏–π –ø–∞—Ä–∞–º–µ—Ç—Ä, –ø—Ä–∏–π–º–∞—î –∞–±–æ —Å—Ç—Ä–æ—á–∫—É, —ñ–º'—è –ø–µ—Ä–µ–º–æ–∂—Ü—è "string" –∞–±–æ –Ω—ñ—á–æ–≥–æ "undefined", —É –≤–∏–ø–∞–¥–∫—É –Ω—ñ—á–∏—î—ó.
     */
    createResultTitle = championName => {    
        const $resultTitle = createNewTag({
            tagName: 'div',
            className: 'resultTitle',
        });

        $resultTitle.innerHTML = (championName) ? championName + ' wins!' : 'Draw!';
        this.addingToArena($resultTitle);
    }

    /**
     * –§—É–Ω–∫—Ü—ñ—è –¥–ª—è –≤–∏–∑–Ω–∞—á–µ–Ω–Ω—è –ø–µ—Ä–µ–º–æ–∂—Ü—è —ñ –ø—Ä–æ–≥—Ä–∞–≤—à–æ–≥–æ.
     * 
     * @param {object} player1 - –û–±–µ–∫—Ç –≥—Ä–∞–≤—Ü—è ‚Ññ1. 
     * @param {object} player2 - –û–±–µ–∫—Ç –≥—Ä–∞–≤—Ü—è ‚Ññ2.
     */
    showResult = (player1, player2) => {
        let champion, lose;

        if (player2.hp === 0 && player2.hp < player1.hp) { // –ü–µ—Ä–µ–º—ñ–≥ —Ä–µ–∞–ª—å–Ω–∏–π –≥—Ä–∞–≤–µ—Ü—å.
            champion = player1.name;
            lose = player2.name;

            this.createResultTitle(champion); 
            this.$player1.querySelector('.character img').src = player1.victory;
            this.$player2.querySelector('.character img').src = player2.falling;

            createSoundTrack({
                className: 'sound-finish',
                src: soundsWins[`soundWins${camelize(formattingPlayerName(champion), true)}`],
            });
        } else if (player1.hp === 0 && player1.hp < player2.hp) { // –ü–µ—Ä–µ–º—ñ–≥ –∫–æ–º–ø'—é—Ç–µ—Ä.
            champion = player2.name;
            lose = player1.name;

            this.createResultTitle(champion); 
            this.$player1.querySelector('.character img').src = player1.falling;
            this.$player2.querySelector('.character img').src = player2.victory;

            createSoundTrack({
                className: 'sound-finish',
                src: soundsWins[`soundWins${camelize(formattingPlayerName(champion), true)}`],
            });
        } else if (player1.hp === 0 && player2.hp === 0) { // –ù—ñ—á–∏—è.
            this.createResultTitle(); 
            this.$player1.querySelector('.character img').src = player1.victory;
            this.$player2.querySelector('.character img').src = player2.victory;

            createSoundTrack({
                className: 'sound-finish',
                src: soundsWins[`soundWins${camelize(formattingPlayerName(player1.name), true)}`],
            });

            createSoundTrack({
                className: 'sound-finish',
                src: soundsWins[`soundWins${camelize(formattingPlayerName(player2.name), true)}`],
            });
        }

        if (player1.hp === 0 || player2.hp === 0) { // –ö—ñ–Ω–µ—Ü—å –≥—Ä–∏.
            this.gameOver(champion, lose);
            
            clearGarbageTags('.sound-arena-bg');

            createSoundTrack({
                className: 'sound-finish',
                src: soundMortalKombat,
            });
        } else {
            this.showControls(); // –í—ñ–¥–æ–±—Ä–∞–∑–∏—Ç–∏ –ø–∞–Ω–µ–ª—å –∫–µ—Ä—É–≤–∞–Ω–Ω—è.
        }
    }

    /**
     * –§—É–Ω–∫—Ü—ñ—è –¥–ª—è —Å—Ç–≤–æ—Ä–µ–Ω–Ω—è –∫–Ω–æ–ø–∫–∏ 'Restart' —â–æ–± –ø–µ—Ä–µ–∑–∞–≤–∞–Ω—Ç–∞–∂–∏—Ç–∏ —Å—Ç–æ—Ä—ñ–Ω–∫—É —ñ –ø–æ—á–∞—Ç–∏ –Ω–æ–≤—É –≥—Ä—É.
     */
    createReloadButton = () => {
        const $reloadWrap = createNewTag({
            tagName: 'div',
            className: 'reloadWrap',
        });    
        const $reloadButton = createNewTag({
            tagName: 'button',
            className: 'button',
            attrList: {
                className: 'button', 
                type: 'button',
            },
        });

        $reloadButton.innerText = 'Restart';
        $reloadButton.addEventListener('click', () => window.location.pathname = 'index.html'); // –ü–µ—Ä–µ–Ω–∞–ø—Ä–∞–≤–ª–µ–Ω–Ω—è –Ω–∞ —Å—Ç–æ—Ä—ñ–Ω–∫—É.
        $reloadWrap.append($reloadButton);
        this.addingToArena($reloadWrap);
    }
    
    /**
     * –§—É–Ω–∫—Ü—ñ—è –¥–ª—è –∑–∞–≤–µ—Ä—à–µ–Ω–Ω—è –≥—Ä–∏ –ø—ñ—Å–ª—è –∑–∞–∫—ñ–Ω—á–µ–Ω–Ω—è –±–æ—é.
     * 
     * @param {string} championName - –Ü–º'—è –ø–µ—Ä–µ–º–æ–∂—Ü—è. 
     * @param {string} loseName - –Ü–º'—è –ø—Ä–æ–≥—Ä–∞–≤—à–æ–≥–æ.
     */
    gameOver = (championName, loseName) => {
        this.$btn.disabled = true; // –ö–Ω–æ–ø–∫–∞ 'Fight' —Å—Ç–∞—î –Ω–µ –∞–∫—Ç–∏–≤–Ω–∞.
        this.logs.generateLogs({type: (championName && loseName) ? 'end' : 'draw', championName, loseName}); // –ì–µ–Ω–µ—Ä–∞—Ü—ñ—è —Ñ—ñ–Ω–∞–ª—å–Ω–æ–≥–æ –ª–æ–≥–∞.
        this.createReloadButton(); // –°—Ç–≤–æ—Ä–µ–Ω–Ω—è –∫–Ω–æ–ø–∫–∏ 'Restart'.
    }
}

export default Game;
