import Game from './modules/class/Game.js';

window.addEventListener('load', () => { 
    const $ARENA = document.querySelector('.arenas');
    const $CHAT = document.querySelector('.chat');

    const player1Id = JSON.parse(localStorage.getItem('player1Id')); // –ë–µ—Ä–µ–º–æ id –≥–µ—Ä–æ—è1 —ñ–∑ localStorage.
    const player2Id = JSON.parse(localStorage.getItem('player2Id')); // –ë–µ—Ä–µ–º–æ id –≥–µ—Ä–æ—è2 —ñ–∑ localStorage.

    const newGame = new Game({
        player1Id, // –ì—Ä–∞–≤—Ü–µ–º ‚Ññ1 –æ–±—Ä–∞–Ω–æ –≥–µ—Ä–æ—è ...
        player2Id, // –ì—Ä–∞–≤—Ü–µ–º ‚Ññ2 –æ–±—Ä–∞–Ω–æ –≥–µ—Ä–æ—è ...
        $arena: $ARENA,
        $chat: $CHAT,
    });
    
    newGame.start();
    //console.log(newGame);
});

